import { LocalStorage } from "jz-tool-lib";
import { toPayApi } from "@api/financial-center-api";
import { PayType } from "@views/financial-center/fund-management/recharge/types";

const topicMap = {
  ALIPCPAY: "agency_ali_recharge_pay_topic",
  WXPCPAY: "agency_wx_recharge_pay_topic",
};

export function getPayResult(payType: PayType, totalFee: number) {
  const agentId = LocalStorage.getItem("bd_agentId");
  const params: any = {
    companyId: agentId,
    profileId: agentId,
    payType,
    subject: "企业充值",
    totalFee,
    topic: topicMap[payType],
  };
  if (params.payType === "ALIPCPAY") {
    params.returnPageType = "agency";
  }
  return toPayApi(params).then((res) => {
    return res?.data?.result || "";
  });
}

export const PAY_SUCCESS_REFRESH_KEY = Symbol("支付成功后，关闭弹框且刷新余额");
