<script setup lang="ts">
import { useQRCode } from "@assets/useHook";
import { inject, onBeforeUnmount, onMounted, ref } from "vue";
import {
  getPayResult,
  PAY_SUCCESS_REFRESH_KEY,
} from "@views/financial-center/fund-management/recharge/compose";

const props = defineProps<{
  money: number;
}>();
const handlePaySuccess = inject<() => void>(PAY_SUCCESS_REFRESH_KEY);

function handlePay() {
  handlePaySuccess?.();
}

onMounted(() => {
  getPayResult("WXPCPAY", props.money).then((text) => {
    try {
      useQRCode({
        dom: "qrcode",
        text,
      });
    } catch (e) {
      console.warn(e);
    }
  });
});

onBeforeUnmount(() => {
  console.log("BeforeUnmount trigger");
  try {
    document.getElementById("qrcode")!.innerHTML = "";
  } catch (e) {
    console.warn(e);
  }
});
</script>

<template>
  <div class="wxpay-block">
    <h2>请使用 微信支付 完成付款</h2>
    <p class="p1">付款金额</p>
    <span>￥{{ props.money }}</span>
    <div class="qr-code" id="qrcode"></div>
    <p class="p2">请使用 微信 扫一扫二维码完成支付</p>
    <p class="p2">
      (微信余额与银行卡支付限额：6000元/笔、9000元/日；零钱通支付限额：200000元/日)
    </p>
    <div class="action-box">
      <el-button style="width: 124px" type="primary" @click="handlePay"
        >已完成支付</el-button
      >
      <slot name="change-pay-method"></slot>
    </div>
  </div>
</template>

<style scoped lang="scss">
.wxpay-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  .p1 {
    font-size: 14px;
    font-weight: 400;
    color: #242934;
    line-height: 14px;
    margin: 20px 0 12px;
  }
  > span {
    font-size: 26px;
    font-weight: 600;
    color: #f12525;
    line-height: 26px;
    margin-bottom: 10px;
  }
  .p2 {
    font-size: 14px;
    font-weight: 400;
    color: #9ca6b9;
    line-height: 14px;
    margin-top: 24px;
  }
  .action-box {
    margin-top: 18px;
  }
}

.qr-code {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 168px;
  height: 168px;
  border: 1px solid #e1e4e8;
}
</style>
