<template>
  <div class="recharge-page">
    <h2 class="mb-20px">账号额度</h2>
    <el-card shadow="never" style="width: 900px">
      <div class="account-limit">
        <div class="item">
          <h3>客户余额</h3>
          <span>￥{{ toMoney(infoData.balance) }}</span>
        </div>
        <div class="item">
          <h3>
            可用余额
            <el-tooltip
              class="box-item"
              effect="dark"
              content="可用余额 = 客户余额 + 信用额度 - 保证金（返佣金额 * X%）"
              placement="top"
            >
              <el-icon><QuestionFilled /></el-icon>
            </el-tooltip>
          </h3>
          <span>￥{{ toMoney(infoData.availableBalance) }}</span>
        </div>
        <div class="item">
          <h3>
            保证金
            <el-tooltip
              class="box-item"
              effect="dark"
              content="为保障客户服务，数企扣留一部分代理商金额作为保障保证金"
              placement="top"
            >
              <el-icon><QuestionFilled /></el-icon>
            </el-tooltip>
          </h3>
          <span>￥{{ toMoney(infoData.ensureBalance) }}</span>
        </div>
      </div>
    </el-card>

    <h2 class="mb-20px mt-20px">充值方式</h2>
    <div class="recharge-way">
      <div class="offline-pay" data-pay="线下支付：">
        <div
          class="item"
          :class="logicalBus.bankPay && 'active'"
          @click="setActivePay('ZGYH')"
        >
          <img src="@/assets/images/pay_zgyh.png" alt="" />
        </div>
      </div>
      <div class="online-pay" data-pay="线上支付：">
        <div
          class="item"
          :class="logicalBus.alipay && 'active'"
          @click="setActivePay('ALIPCPAY')"
        >
          <img src="@/assets/images/alpay.png" alt="" />
        </div>
        <div
          class="item"
          :class="logicalBus.wxPay && 'active'"
          @click="setActivePay('WXPCPAY')"
        >
          <img src="@/assets/images/wx-pay.png" alt="" />
        </div>
      </div>
    </div>

    <div class="pay-details mt-30px">
      <div v-if="logicalBus.onlinePay" class="online-pay-details">
        <p class="info-desc">企业账户当前余额：{{ infoData.balance }}元</p>
        <el-input
          class="w-350px"
          v-model="rechargeValue"
          placeholder="请输入充值金额"
        />
        <el-button style="width: 100px" type="primary" @click="handleOnlinePay"
          >确定</el-button
        >
      </div>
      <div v-else class="offline-pay-details">
        <p class="info-desc mb-20px">
          您线下打款，数企在收到汇款后将自动充值到您的企业余额账户中。
        </p>
        <el-card class="bank-card">
          <img src="@/assets/images/pay_zgyh.png" alt="" />
          <p
            style="font-weight: bold; font-size: 22px; margin-bottom: 20px"
            class="font-regular"
          >
            {{ rechargeInfo.virtualAccount }}
            <el-link type="primary" @click="handleCopy">复制</el-link>
          </p>
          <p><label>收款户名：</label>八度云计算（安徽）有限公司</p>
          <p><label>收款银行：</label>中国银行合肥望江中路支行</p>
        </el-card>
      </div>
    </div>
  </div>

  <PaymentInProgressDialog
    ref="paymentInProgressDialogRef"
    :money="rechargeValue"
    :pay-method="activePay"
  />
</template>
<script setup lang="ts">
import { QuestionFilled } from "@element-plus/icons-vue";
import { computed, reactive, ref, provide } from "vue";
import { ElMessage } from "element-plus";
import {
  assetsInfoApi,
  getCompanyVirtualAccountByAgencyIdApi,
} from "@api/financial-center-api";
import PaymentInProgressDialog from "@views/financial-center/fund-management/recharge/PaymentInProgressDialog.vue";
import { PayType } from "@views/financial-center/fund-management/recharge/types";
import { PAY_SUCCESS_REFRESH_KEY } from "@views/financial-center/fund-management/recharge/compose";
import { copyText, LocalStorage, toMoney } from "jz-tool-lib";

const activePay = ref<PayType>("ZGYH");
const rechargeValue = ref("");
const paymentInProgressDialogRef = ref();
// 校验金额的正则表达式
const amountRegex = /^(?:0|[1-9]\d*)(?:\.\d{1,2})?$/;
const rechargeInfo = reactive({
  virtualAccount: "",
});
const agentId = LocalStorage.getItem("bd_agentId");
const infoData = reactive({
  availableBalance: 0,
  balance: 0,
  ensureBalance: 0,
});
const logicalBus = computed(() => {
  const bankPay = activePay.value === "ZGYH";
  const alipay = activePay.value === "ALIPCPAY";
  const wxPay = activePay.value === "WXPCPAY";
  const onlinePay = alipay || wxPay;

  return {
    bankPay,
    alipay,
    wxPay,
    onlinePay,
  };
});

function handleCopy() {
  const text = `账号：${rechargeInfo.virtualAccount}，收款户名：八度云计算（安徽）有限公司，收款银行：中国银行合肥望江中路支行`;
  copyText(text)
    .then(() => {
      ElMessage.success("复制成功");
    })
    .catch(() => {
      ElMessage.success("复制失败");
    });
}

function setActivePay(pay: PayType) {
  activePay.value = pay;
}

function handleOnlinePay() {
  if (amountRegex.test(rechargeValue.value)) {
    paymentInProgressDialogRef.value.toggle();
  } else {
    ElMessage.error("请输入正确的金额");
  }
}

function getCompanyVirtualAccountByAgencyId() {
  getCompanyVirtualAccountByAgencyIdApi().then((res) => {
    console.log(res, "line 141");
    rechargeInfo.virtualAccount = res?.data?.virtualAccount || "";
  });
}

function assetsInfo() {
  assetsInfoApi({
    agencyId: agentId,
  }).then((res) => {
    console.log(res, "line 143");
    infoData.availableBalance = res?.data?.availableBalance || 0;
    infoData.balance = res?.data?.balance || 0;
    infoData.ensureBalance = res?.data?.ensureBalance || 0;
  });
}

provide(PAY_SUCCESS_REFRESH_KEY, () => {
  paymentInProgressDialogRef.value.toggle(false);
  assetsInfo();
});

getCompanyVirtualAccountByAgencyId();
assetsInfo();
</script>
<style scoped lang="scss">
.mb-20px {
  margin-bottom: 20px;
}
.mt-20px {
  margin-top: 20px;
}
.mt-30px {
  margin-top: 30px;
}

.w-350px {
  width: 350px;
  :deep(.el-input__wrapper) {
    width: 350px;
  }
}

.account-limit {
  display: flex;
  justify-content: space-between;
  .item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    h3 {
      margin-bottom: 12px;
      :deep(.el-icon) {
        color: #9ca6b9;
      }
    }
  }
}

.recharge-way {
  display: flex;
  column-gap: 40px;
  .offline-pay,
  .online-pay {
    padding-top: 30px;
    position: relative;
    display: flex;
    column-gap: 20px;
    &::before {
      position: absolute;
      top: 0;
      left: 0;
      content: attr(data-pay);
    }

    .item {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 228px;
      height: 96px;
      cursor: pointer;
      background: #ffffff;
      border-radius: 4px;
      border: 1px solid #e1e4e8;

      &:hover,
      &.active {
        border-color: #0056ff;

        &::before {
          position: absolute;
          right: -1px;
          bottom: -1px;
          content: "";
          width: 20px;
          height: 20px;
          background: url("@/assets/images/select-icon.png") no-repeat center;
          background-size: 100%;
        }
      }

      img {
        height: 40px;
      }
    }
  }
}

.pay-details {
  .offline-pay-details {
    width: 600px;
    .info-desc {
      color: #9ca6b9;
    }
  }

  .online-pay-details {
    > * {
      display: block;
      margin-bottom: 20px;
    }
  }
}

.bank-card {
  width: 400px;

  :deep(.el-card__body) {
    padding: 24px;
  }

  img {
    width: 150px;
    margin-bottom: 20px;
  }
  p {
    display: flex;
    align-items: center;
    ~ p {
      margin-top: 10px;
    }
    label {
      font-weight: bold;
    }
    :deep(.el-link) {
      margin-left: 10px;
    }
  }
}
</style>
