<script setup lang="ts">
import { computed, ref, watch } from "vue";
import ChangePaymentMethodDialog from "@views/financial-center/fund-management/recharge/change-payment/ChangePaymentMethodDialog.vue";
import AlipayPaymentComponent from "@views/financial-center/fund-management/recharge/change-payment/AlipayPaymentComponent.vue";
import WxPaymentComponent from "@views/financial-center/fund-management/recharge/change-payment/WxPaymentComponent.vue";
import { PayType } from "@views/financial-center/fund-management/recharge/types";

const props = defineProps<{
  money: number | string;
  payMethod: PayType;
}>();

const paymentMap = {
  ALIPCPAY: AlipayPaymentComponent,
  WXPCPAY: WxPaymentComponent,
};

const visible = ref(false);
const activePay = ref<PayType>("ALIPCPAY");
const currentPayment = computed(() => paymentMap[activePay.value]);

watch(
  () => visible.value,
  (val) => {
    if (val) {
      setActivePay(props.payMethod);
    }
  }
);

function handleOk() {}

function toggle(val = true) {
  visible.value = val;
}

function setActivePay(pay: PayType) {
  activePay.value = pay;
}

defineExpose({
  toggle,
});
</script>

<template>
  <el-dialog
    v-model="visible"
    title="立即充值"
    class="custom-dialog"
    :width="700"
    destroy-on-close
  >
    <component :is="currentPayment" :money="props.money">
      <template #change-pay-method>
        <ChangePaymentMethodDialog v-model:pay-method="activePay" />
      </template>
    </component>
    <!--    <template #footer>-->
    <!--      <div class="dialog-footer">-->
    <!--        <el-button type="primary" @click="handleOk">确定</el-button>-->
    <!--        <el-button @click="visible = false">关闭</el-button>-->
    <!--      </div>-->
    <!--    </template>-->
  </el-dialog>
</template>

<style scoped lang="scss">
.recharge-way {
  padding: 10px 0;
  .offline-pay {
    margin-top: 30px;
  }

  .offline-pay,
  .online-pay {
    padding-top: 30px;
    position: relative;
    display: flex;
    column-gap: 20px;
    &::before {
      position: absolute;
      top: -6px;
      left: 0;
      content: attr(data-pay);
      font-size: 18px;
    }

    .item {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 228px;
      height: 96px;
      cursor: pointer;
      background: #ffffff;
      border-radius: 4px;
      border: 1px solid #e1e4e8;

      &:hover,
      &.active {
        border-color: #0056ff;

        &::before {
          position: absolute;
          right: -1px;
          bottom: -1px;
          content: "";
          width: 20px;
          height: 20px;
          background: url("@/assets/images/select-icon.png") no-repeat center;
          background-size: 100%;
        }
      }

      img {
        height: 40px;
      }
    }
  }
}
</style>
