<script setup lang="ts">
import { computed, ref } from "vue";
import { PayType } from "@views/financial-center/fund-management/recharge/types";

const props = defineProps<{
  payMethod: PayType;
}>();

const emit = defineEmits(["update:payMethod"]);

const visible = ref(false);
const activePay = computed({
  get() {
    return props.payMethod;
  },
  set(val) {
    emit("update:payMethod", val);
  },
});

const logicalBus = computed(() => {
  const bankPay = activePay.value === "ZGYH";
  const alipay = activePay.value === "ALIPCPAY";
  const wxPay = activePay.value === "WXPCPAY";
  const onlinePay = alipay || wxPay;

  return {
    bankPay,
    alipay,
    wxPay,
    onlinePay,
  };
});

function handleOk() {}

function toggle(val = true) {
  visible.value = val;
}

function setActivePay(pay: PayType) {
  activePay.value = pay;
}

defineExpose({
  toggle,
  setActivePay,
  getActivePay() {
    return activePay.value;
  },
});
</script>

<template>
  <el-button @click="toggle">更改支付方式</el-button>

  <el-dialog
    v-model="visible"
    title="选择支付方式"
    class="custom-dialog"
    :width="700"
  >
    <div class="recharge-way">
      <div class="online-pay" data-pay="线上支付：">
        <div
          class="item"
          :class="logicalBus.alipay && 'active'"
          @click="setActivePay('ALIPCPAY')"
        >
          <img src="../../../../../assets/images/alpay.png" alt="" />
        </div>
        <div
          class="item"
          :class="logicalBus.wxPay && 'active'"
          @click="setActivePay('WXPCPAY')"
        >
          <img src="../../../../../assets/images/wx-pay.png" alt="" />
        </div>
      </div>
      <!--      <div class="offline-pay" data-pay="线下支付：">-->
      <!--        <div-->
      <!--          class="item"-->
      <!--          :class="logicalBus.bankPay && 'active'"-->
      <!--          @click="setActivePay('ZGYH')"-->
      <!--        >-->
      <!--          <img src="../../../../../assets/images/pay_zgyh.png" alt="" />-->
      <!--        </div>-->
      <!--      </div>-->
    </div>

    <!--    <template #footer>-->
    <!--      <div class="dialog-footer">-->
    <!--        <el-button type="primary" @click="handleOk">确定</el-button>-->
    <!--        <el-button @click="visible = false">关闭</el-button>-->
    <!--      </div>-->
    <!--    </template>-->
  </el-dialog>
</template>

<style scoped lang="scss">
.recharge-way {
  padding: 10px 0;
  .offline-pay {
    margin-top: 30px;
  }

  .offline-pay,
  .online-pay {
    padding-top: 30px;
    position: relative;
    display: flex;
    column-gap: 20px;
    &::before {
      position: absolute;
      top: -6px;
      left: 0;
      content: attr(data-pay);
      font-size: 18px;
    }

    .item {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 228px;
      height: 96px;
      cursor: pointer;
      background: #ffffff;
      border-radius: 4px;
      border: 1px solid #e1e4e8;

      &:hover,
      &.active {
        border-color: #0056ff;

        &::before {
          position: absolute;
          right: -1px;
          bottom: -1px;
          content: "";
          width: 20px;
          height: 20px;
          background: url("@assets/images/select-icon.png") no-repeat center;
          background-size: 100%;
        }
      }

      img {
        height: 40px;
      }
    }
  }
}
</style>
