<script setup lang="ts">
import { inject, onMounted, ref } from "vue";
import {
  getPayResult,
  PAY_SUCCESS_REFRESH_KEY,
} from "@views/financial-center/fund-management/recharge/compose";

const props = defineProps<{
  money: number;
}>();
const handlePaySuccess = inject<() => void>(PAY_SUCCESS_REFRESH_KEY);
const payResult = ref("");
const payStatus = ref(false); // 前端自定义的支付状态（优化交互用的）

function handlePay() {
  if (payStatus.value) {
    handlePaySuccess?.();
  } else {
    window.open(
      `https://subapp.bdsaas.com:1893/app-core?pay=1&html=${encodeURIComponent(
        payResult.value
      )}`
    );
    setTimeout(() => {
      payStatus.value = true; // 优化交互
    }, 300);
  }
}

onMounted(() => {
  getPayResult("ALIPCPAY", props.money).then((res) => {
    payResult.value = res;
  });
  payStatus.value = false;
});
</script>

<template>
  <div class="alipay-block">
    <h2>请使用 支付宝 完成付款</h2>
    <p class="p1">付款金额</p>
    <span>￥{{ props.money }}</span>
    <p class="p2">
      {{
        payStatus
          ? "支付成功？请点击下方按钮返回"
          : "请点击下方按钮使用 支付宝 完成支付"
      }}
    </p>
    <div class="action-box">
      <el-button style="width: 124px" type="primary" @click="handlePay">{{
        payStatus ? "已完成支付" : "去支付"
      }}</el-button>
      <slot name="change-pay-method"></slot>
    </div>
  </div>
</template>

<style scoped lang="scss">
.alipay-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  .p1 {
    font-size: 14px;
    font-weight: 400;
    color: #242934;
    line-height: 14px;
    margin: 20px 0 12px;
  }
  > span {
    font-size: 26px;
    font-weight: 600;
    color: #f12525;
    line-height: 26px;
    margin-bottom: 10px;
  }
  .p2 {
    font-size: 14px;
    font-weight: 400;
    color: #9ca6b9;
    line-height: 14px;
    margin-top: 24px;
  }
  .action-box {
    margin-top: 18px;
  }
}
</style>
